<template>
	<picker mode="multiSelector" :range="range" :value="rangeValue" range-key="label" @change="onChange" @columnchange="onColumnchange">
		<slot></slot>
	</picker>
</template>

<script>
	import cityData from './city.json'
	
	export default {
		name: 'CityPicker',
		data() {
			return {
				range: [[], [], []],
				rangeValue: [0, 0, 0],
				currentTotalAddress: ''
			}
		},
		beforeMount() {
			this.initRange()
		},
		methods: {
			initRange() {
				let provinceList = cityData
				let cityList = provinceList[0].children
				let areaList = cityList[0].children
				this.range = [
					cityData,
					cityList,
					areaList
				]
			},
			onChange(e) {
				const result = e.detail.value.map((index, i) => {
					return this.range[i] ? (this.range[i][index] || {}) : {}
				})
				this.$emit('change', result)
			},
			onColumnchange(e) {
				const { column, value } = e.detail
				if (column === 0) {
					// 修改省数据
					const provinceList = this.range[0]
					let cityList = provinceList[value].children || []
					let areaList = cityList[0] ? cityList[0].children : []
					this.range = [provinceList, cityList, areaList]
					this.rangeValue = [value, 0, 0]
				} else if (column === 1) {
					// 修改市数据
					let areaList = this.range[1][value] ? this.range[1][value].children : []
					this.range = [this.range[0], this.range[1], areaList]
					this.rangeValue = [this.rangeValue[0], value, 0]
				} else {
					this.rangeValue = [this.rangeValue[0], this.rangeValue[1], value]
				}
				this.$forceUpdate()
			}
		}
	}
</script>

<style lang="scss" scoped>
	
</style>
